*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
}

html {
  margin: 0 !important;
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (max-width: 37.5em) {
  html {
    font-size: 43.75%;
  }
}

body {
  box-sizing: border-box;
}

body {
  font-family: "Inter var" !important;
}

.btn {
  text-transform: uppercase;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
}
.btn--subscribe {
  background-color: #7fa1c3 !important;
  border: none !important;
  color: #fff !important;
  padding: 1.5rem 2rem !important;
  border-radius: 0.7rem !important;
  transition: all 0.3s !important;
  font-size: 1.6rem !important;
  will-change: transform;
  box-shadow: 0.2rem 0.6rem 1rem rgba(44, 44, 44, 0.15);
}
.btn--subscribe:hover {
  transform: translateY(-2px);
  box-shadow: 0.2rem 1rem 1rem rgba(44, 44, 44, 0.15);
}
.btn--subscribe:active {
  transform: scale(0.97);
}

header {
  height: 100vh;
}

.navigation {
  position: relative;
  background-color: #fff;
}

.nav-inner {
  height: 15vh;
}

.row {
  height: 85vh;
}

.search-icon {
  font-size: 1.8rem;
  cursor: pointer;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  height: 100%;
  transition: all 0.3s;
}
.search-icon:active {
  transform: scale(0.9);
}

.search-bar {
  position: absolute;
  display: none;
  top: 0;
  right: 0;
  width: 35%;
  background-color: #fff;
  z-index: 100;
  height: 100vh;
}

.cross-icon {
  position: absolute;
  top: 0%;
  right: 3%;
  font-size: 3.5rem;
}

.search-bar-input {
  padding: 1.5rem;
  margin-top: 7rem;
  width: 90%;
  font-size: 1.6rem;
  border: transparent;
}
.search-bar-input:focus {
  outline: none;
}

.search-bar-outter {
  margin: 0 3rem;
  padding: 0 1rem;
  border-bottom: 0.2rem solid black;
}

.hero-section {
  background-color: #eaeaea;
}
.hero-section__right {
  height: 100%;
  display: flex;
  justify-content: center;
}
.hero-section__img {
  height: 100%;
}
.hero-section__left {
  position: relative;
}
.hero-section__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}